<script>
export default {
  
  data(){
   const generateData = _ => {
        const data = [];
        for (let i = 1; i <= 15; i++) {
          data.push({
            key: i,
            label: `备选项 ${ i }`,
            disabled: i % 4 === 0
          });
        }
        return data;
      }; 
    return { form:{
        search:""
      },

      dat: generateData(),
      valu: [1, 4],
      //picker是选择器的意思
      ti:[new Date(2018,10,1,8,20),new Date(2020,10,1,8,20)],//is-range范围
      startTime:"",
      a:3.7,
      color2:null,
       predefineColors: [
          '#ff4500',
          '#ff8c00',
          '#ffd700',
          '#90ee90',
          '#00ced1',
          '#1e90ff',
          '#c71585',
          'rgba(255, 69, 0, 0.68)',
          'rgb(255, 120, 0)',
          'hsv(51, 100, 98)',
          'hsva(120, 40, 94, 0.5)',
          'hsl(181, 100%, 37%)',
          'hsla(209, 100%, 56%, 0.73)',
          '#c7158577'
        ],
     // iconClasses: ['icon-rate-face-1', 'icon-rate-face-2', 'icon-rate-face-3'],
      //colors:["black","blue","red"],
      colors:{2:"black",3:"salmon",4:"red",5:"skyblu"},
      co:null,
      pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
      endTime:"",
      data:"",
      time:new Date().toLocaleString(),
      mar:{
        30:"30分",
        50:"50分",
        40:{//控件基本有change blur focus 
          style:{//marks取值样式
            color:"red"
          },
          label:this.$createElement('strong',"40%")//屏幕阅读器标签
        }
      },
      su:[30,50],//show-stops是断点显示，输入框步伐
      suma:50,
      sumb:30,
      d:100,//:content
      t:true,
      e:this.$store.getters.he,
      data:[],
      value:[
        {
        value:"z",//键名不能相同
        label:"q",//键值用于显示
        children:[//子菜单
          {
            value:"s",
            label:"w"
          },
          {
            value:"e",
            label:"f"
          }
        ]
        },
         {
        value:"r",
        label:"q",
        children:[
          {
            value:"s",
            label:"w"
          },
          {
            value:"e",
            label:"f"
          }
        ]
        }
      ]
    }
  },
  methods:{
    
  }
}
</script>




